:root {
	--primary-color: #e50914;
	--dark-color: #141414;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: 'Arial', sans-serif;
	-webkit-font-smoothing: antialiased;
	background: black;
	color: gray;
}

ul {
	list-style: none;
}

h1,
h2,
h3,
h4 {
	color: white;
}

a {
	color: white;
	text-decoration: none;
}

p {
	margin: 0.5rem 0;
}

img {
	width: 100%;
}

.showcase {
	width: 100%;
	height: 100vh;
	position: relative;
	background: url('https://i.ibb.co/vXqDmnh/background.jpg') no-repeat center
		center/cover;
	/* border-bottom: 8px solid #222222; */
}

.showcase::after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	background: rgba(0, 0, 0, 0.6);
	box-shadow: inset 0px 0px 250px #000000, inset -0px -0px 250px #000000;
}

.showcase-top {
	position: relative;
	height: 90px;
	z-index: 2;
}

.showcase-top img {
	width: 170px;
	position: absolute;
	top: 30%;
	left: 3.5%;
	/* transform: translate(-50%, -50%); */
}

.showcase-top a {
	position: absolute;
	top: 50%;
	right: 0;
	transform: translate(-50%, -50%);
}

.showcase-content {
	position: relative;
	margin: auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-top: 6.5rem;
	z-index: 2;
}

.showcase-content h1 {
	text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.7);
	font-weight: 700;
	font-size: 4rem;
	line-height: 1.1;
	margin: 0 0 2rem;
	width: 70%;
}

.showcase-content p {
	color: white;
	font-weight: 400;
	font-size: 1.9rem;
	line-height: 1.25;
	margin: 0 0 2rem;
}

.tabs {
	background: var(--dark-color);
	padding-top: 1rem;
	border-top: 6px solid #3d3d3d;
}

.tabs .container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 1rem;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.tabs p {
	font-size: 1.2rem;
	padding-top: 0.5rem;
}

.tabs .container > div {
	padding: 1.5rem;
}

.tabs .container > div:hover {
	color: white;
	cursor: pointer;
}

.tab-content {
	padding: 3rem 0;
	color: white;
	background: black;
}

#tab-1-content .tab-1-content-inner {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 2rem;
	justify-content: center;
	align-items: center;
}

#tab-2-content .tab-2-content-top {
	display: grid;
	grid-template-columns: 2fr 1fr;
	grid-gap: 1rem;
	justify-content: center;
	align-items: center;
}

#tab-2-content .tab-2-content-bottom {
	margin-top: 2rem;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 2rem;
	justify-content: center;
	align-items: center;
	text-align: center;
}

/* Table Styling */

.table {
	width: 100%;
	margin-top: 2rem;
	border-collapse: collapse;
	border-spacing: 0;
}

.table thead th {
	text-transform: uppercase;
	padding: 0.8rem;
}

.table tbody tr td {
	color: #999;
	padding: 0.8rem 1.2rem;
	text-align: center;
}

.table tbody tr td:first-child {
	text-align: left;
}

.table tbody tr:nth-child(odd) {
	background: #222222;
}

/* Footer Styling */

.footer {
	max-width: 75%;
	margin: 1rem auto;
	overflow: auto;
}

.footer,
.footer a {
	color: #999;
	font-size: 0.9rem;
}

.footer p {
	margin-bottom: 1.5rem;
}

.footer .footer-cols {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grip-gap: 2rem;
}

.footer li {
	line-height: 1.9;
}

.container {
	max-width: 80%;
	margin: auto;
	overflow: hidden;
	padding: 0 2rem;
}

/* Text Styling */

.text-xl {
	font-size: 2rem;
	margin-bottom: 1rem;
}

.text-lg {
	font-size: 1.8em;
	margin-bottom: 1rem;
}

.text-md {
	font-size: 1.5rem;
	margin-bottom: 1rem;
}

.text-center {
	text-align: center;
}

.text-dark {
	color: #999999;
}

/* Button Styling */

.btn {
	display: inline-block;
	background: var(--primary-color);
	color: white;
	padding: 0.4rem 1.3rem;
	font-size: 1rem;
	text-align: center;
	border: none;
	cursor: pointer;
	margin-right: 0.5rem;
	outline: none;
	box-shadow: 0 1px 0 rgba(0, 0, 0, 0.5);
	border-radius: 2px;
}

.btn:hover {
	opacity: 0.9;
}

.btn-rounded {
	border-radius: 5px;
}

.btn-xl {
	font-size: 2rem;
	padding: 1.3rem 1.8rem;
	text-transform: uppercase;
}

.btn-lg {
	font-size: 1rem;
	padding: 0.8rem 1.3rem;
	text-transform: uppercase;
}

.tabs-buttons {
	letter-spacing: 3px;
}

@media (max-width: 960px) {
	.showcase {
		height: 80vh;
	}

	.hide-sm {
		display: none;
	}

	.showcase-content h1 {
		font-size: 3.2rem;
		line-height: 1;
	}

	.footer .footer-cols {
		grid-template-columns: repeat(2, 1fr);
	}

	.btn-xl {
		font-size: 1.5rem;
		padding: 1.4rem 2rem;
	}

	.text-xl {
		font-size: 1.5rem;
	}

	.text-lg {
		font-size: 1.3rem;
	}

	.text-md {
		font-size: 1rem;
	}
}

@media (max-width: 700px) {
	#tab-1-content .tab-1-content-inner {
		grid-template-columns: 1fr;
		text-align: center;
	}

	#tab-2-content .tab-2-content-top {
		display: block;
		text-align: center;
	}

	#tab-2-content .tab-2-content-bottom {
		grid-template-columns: 1fr;
	}
}
